/**
 *
 * Delay Loading - Image
 *
 * @author Takuto Yanagida
 * @version 2021-12-26
 *
 */


'use strict';

window['NACSS']          = window['NACSS']          || {};
window['NACSS']['delay'] = window['NACSS']['delay'] || {};


((NS) => {

	/**
	 *
	 * Style Class Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function hasClass(tar, cls) {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			return tar.dataset[key] !== undefined;
		} else {
			return tar.classList.contains(key);
		}
	}
	
	function setClass(tar, cls, enabled = true, val = '') {
		const key = cls.substr(1);
		if (cls.startsWith(':')) {
			if (enabled) {
				tar.dataset[key] = val;
			} else {
				delete tar.dataset[key];
			}
		} else {
			const c = [key, val].join('-');
			if (enabled) {
				tar.classList.add(c);
			} else {
				tar.classList.remove(c);
			}
		}
	}
	
	function getSelector(cls) {
		if (cls.startsWith(':')) {
			return `*[data-${cls.substr(1).replace(/([A-Z])/g, c => '-' + c.charAt(0).toLowerCase())}]`;
		} else {
			return `*${cls}`;
		}
	}
	
	/**
	 *
	 * Utilities
	 *
	 * @author Takuto Yanagida
	 * @version 2021-12-06
	 *
	 */
	
	
	function getScrollOffset() {
		const s   = getComputedStyle(document.documentElement);
		const val = parseInt(s.scrollPaddingTop);
		return Number.isNaN(val) ? 0 : val;
	}
	
	
	// -----------------------------------------------------------------------------
	
	
	function throttle(fn) {
		let isRunning;
		return (...args) => {
			if (isRunning) return;
			isRunning = true;
			requestAnimationFrame(() => {
				isRunning = false;
				fn(...args);
			});
		};
	}
	
	const resizeListeners = [];
	
	function onResize(fn, doFirst = false) {
		if (doFirst) fn();
		resizeListeners.push(throttle(fn));
	}
	
	document.addEventListener('DOMContentLoaded', () => {
		window.addEventListener('resize', () => {
			for (const l of resizeListeners) l();
		}, { passive: true });
	});
	
	
	// -----------------------------------------------------------------------------
	
	
	function onIntersect(fn, targets, threshold = 0, rootMargin ='* 0px 0px 0px') {
		const vs = Array(targets.length).fill(false);
		const tm = new Map();
		targets.forEach((t, i) => tm.set(t, i));
	
		function init() {
			const io = new IntersectionObserver((es) => {
				for (const e of es) vs[tm.get(e.target)] = e.isIntersecting;
				fn(vs);
			}, { rootMargin: rootMargin.replace('*', `${mt}px`), threshold });
			for (const t of targets) io.observe(t);
			return io;
		}
		let mt = 0;
		let io = null;
		let st = null;
		onResize(() => {
			mt = -getScrollOffset();
			if (st) clearTimeout(st);
			st = setTimeout(() => {
				if (io) io.disconnect();
				io = init();
			}, 100);
		}, true);
	}
	

	/**
	 *
	 * Image
	 *
	 * @author Takuto Yanagida
	 * @version 2022-07-11
	 *
	 */
	
	
	const BLANK_IMG = 'data:image/gif;base64,R0lGODdhAQABAPAAAP///wAAACwAAAAAAQABAEACAkQBADs=';
	
	
	function apply(ts, opts = {}) {
		if (ts.length === 0) return;
	
		opts = Object.assign({
			offset    : 100,
			blankImage: BLANK_IMG,
			doForce   : true,
		}, opts);
	
		for (const t of ts) {
			if (t.getAttribute('loading')) {
				t.setAttribute('loading', 'lazy');
			}
		}
	
		if (isPolyfillNeeded(HTMLImageElement.prototype)) {  // For old browsers.
			setPolyfill(ts, opts);
		} else if (opts['doForce']) {
			const np = ts.filter(e => !e.getAttribute('loading'));
			setPolyfill(np, opts);
		}
	
		window.addEventListener('beforeprint', () => {
			for (const t of ts) {
				if (t.dataset.src) {
					show(t);
				}
				if (t.getAttribute('loading') === 'lazy') {
					t.setAttribute('loading', 'eager');
				}
			}
		}, false);
	}
	
	function setPolyfill(ts, opts) {
		for (const t of ts) {
			hide(t, opts['blankImage']);
		}
		onIntersect(vs => {
			for (let i = 0; i < ts.length; i += 1) {
				const t = ts[i];
				if (t.dataset.src && vs[i]) {
					show(t);
				}
			}
		}, ts, 0, `* 0px ${opts['offset']}px 0px`);
	}
	
	function hide(t, blankImage) {
		saveAttribute(t, 'src', blankImage);
		saveAttribute(t, 'srcset');
		t.style.opacity = 0;
		const h = t.getAttribute('height');
		if (h) {
			t.style.minHeight = h + 'px';
		}
	}
	
	function show(t) {
		t.addEventListener('load', () => {
			t.style.minHeight = '';
			t.style.opacity   = '';
		});
		restoreAttribute(t, 'src');
		restoreAttribute(t, 'srcset');
	}
	
	NS.applyImage = apply;

	NS.isPolyfillNeededForImage = () => isPolyfillNeeded(HTMLImageElement.prototype);

	/**
	 *
	 * Common Functions
	 *
	 * @author Takuto Yanagida
	 * @version 2021-11-11
	 *
	 */
	
	
	function saveAttribute(t, at, alt = null) {
		const v = t.getAttribute(at);
		if (v) {
			t.dataset[at] = v;
			if (alt) {
				t.setAttribute(at, alt);
			} else {
				t.removeAttribute(at);
			}
		}
	}
	
	function restoreAttribute(t, at) {
		const v = t.dataset[at];
		if (v) {
			t.setAttribute(at, v);
			delete t.dataset[at];
		}
	}
	
	function isPolyfillNeeded(proto) {
		return (!('loading' in proto));
	}
	

})(window['NACSS']['delay']);
